<template>
<example-super-goal :my-list="players">

</example-super-goal>
</template>

<script>
import ExampleSuperGoal from "@/components/main/ExampleSuperGoal";
import {onMounted, reactive} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "SuperGoalPers",
  components: {ExampleSuperGoal},
  setup(){
    const players=reactive([])
    const total=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/2041'),
        axios.get('https://api.footballi.net/api/v2/player/4016'),
        axios.get('https://api.footballi.net/api/v2/player/14894'),
        axios.get('https://api.footballi.net/api/v2/player/2446142'),
      ])
          .then(axios.spread((...response)=>{
            players.push(response)
          }))
          .catch((error)=>{
          handleError(error)
          })

    })
    return{
      players,total
    }
  }
}
</script>

