<template>
<example-transfer :my-list="menu">

</example-transfer>
</template>

<script>
import ExampleTransfer from "@/components/main/ExampleTransfer";
import {reactive,onMounted} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "TransferTeamPers",
  components: {ExampleTransfer},
  setup(){
    const menu=reactive([])
    const sum=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/2440410'),
        axios.get('https://api.footballi.net/api/v2/player/29934'),
        axios.get('https://api.footballi.net/api/v2/player/14897'),
        axios.get('https://api.footballi.net/api/v2/player/2041'),
        axios.get('https://api.footballi.net/api/v2/player/2415911'),
        axios.get('https://api.footballi.net/api/v2/player/2354'),
        axios.get('https://api.footballi.net/api/v2/player/2446242'),
        axios.get('https://api.footballi.net/api/v2/player/2237003'),
        axios.get('https://api.footballi.net/api/v2/player/45941'),
        axios.get('https://api.footballi.net/api/v2/player/2282197'),
        axios.get('https://api.footballi.net/api/v2/player/1580'),
        axios.get('https://api.footballi.net/api/v2/player/2237519'),
        axios.get('https://api.footballi.net/api/v2/player/2258234'),
        axios.get('https://api.footballi.net/api/v2/player/14894'),
      ])
          .then(axios.spread((...response)=>{
            menu.push(response)
          }))
          .catch((error)=>{
            handleError(error)
          })
    })

    return{
      menu,sum
    }
  }
}
</script>

