<template>
<example-team-rank :my-list="list">
  <template #rankName>
    <option class="font-bold text-slate-900 text-sm text-start">لیگ برتر خلیج فارس</option>
  </template>
</example-team-rank>
</template>

<script>
import {onMounted, reactive} from "vue";
import EndPoints from "@/service/EndPoints";
import ExampleTeamRank from "@/components/main/ExampleTeamRank";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "RankPerspolise",
  components:{ExampleTeamRank},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
      EndPoints.getDescriptionPers()
          .then((response)=>{
            if(response.data.success){
              list.push(response.data.data.standing)

            }
          })
          .catch((error)=>{
        handleError(error)
          })

    })
    return{
      list,result
    }
  }
}
</script>

