<template>
<example-attack :my-list="list">
  <template #Midfielder>
    <p class="font-bold text-white font-black w-full">هافبک های تیم پرسپولیس</p>
  </template>
</example-attack>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleAttack from "@/components/main/ExampleAttack";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AttackPers",
  components:{ExampleAttack},
  setup(){
    const list=reactive([]);
    const total=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/1592'),
        axios.get('https://api.footballi.net/api/v2/player/4016'),
        axios.get('https://api.footballi.net/api/v2/player/6291'),
        axios.get('https://api.footballi.net/api/v2/player/6462'),
        axios.get('https://api.footballi.net/api/v2/player/14894'),
        axios.get('https://api.footballi.net/api/v2/player/2226037'),
        axios.get('https://api.footballi.net/api/v2/player/2228465'),
        axios.get('https://api.footballi.net/api/v2/player/2443442'),
        axios.get('https://api.footballi.net/api/v2/player/2446242'),
        axios.get('https://api.footballi.net/api/v2/player/2448566'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      list,total
    }
  }
}
</script>

