<template>
<example-defender :my-list="list">
  <template #defender>
    <p class="font-bold text-white font-black w-full">مدافعان تیم پرسپولیس</p>
  </template>
</example-defender>
</template>

<script>
import ExampleDefender from "@/components/main/ExampleDefender";
import {onMounted, reactive} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "DefenderPers",
  components: {ExampleDefender},
  setup(){
    const list=reactive([])
    const result=onMounted(async ()=>{
    await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/1635'),
        axios.get('https://api.footballi.net/api/v2/player/4710'),
        axios.get('https://api.footballi.net/api/v2/player/6517'),
        axios.get('https://api.footballi.net/api/v2/player/9207'),
        axios.get('https://api.footballi.net/api/v2/player/22876'),
        axios.get('https://api.footballi.net/api/v2/player/2420157'),
        axios.get('https://api.footballi.net/api/v2/player/2446240'),
        axios.get('https://api.footballi.net/api/v2/player/2451834'),
      ])
          .then(axios.spread((...response)=>{
            list.push(response)
          }))
          .catch((error)=>{
          handleError(error)
          })

    })
    return{
      list,result,
    }
  }
}
</script>

