<template>
<section class="center-perspolise">
  <div class="container mx-auto p-2">
    <div class="w-full h-auto">
      <p class="font-semibold text-start text-black w-full">بازیکنان تیم پرسپولیس</p>
      <div class="bg-black h-0.5 w-full my-1"></div>
      <div class="bg-green-500 w-full min-h-full relative px-3">
        <example-goal-keeper :my-list="result">
          <template #goalKeeper>
            <p class="font-bold text-white font-black w-full">دروازه بان های تیم پرسپولیس</p>
          </template>
        </example-goal-keeper>
        <defender-pers></defender-pers>
        <attack-pers></attack-pers>
        <main-attack></main-attack>
        <img :src="require('../../../assets/image/squad-middle.png')" alt="" class="h-auto absolute mx-auto bottom-2.5 w-full left-0 right-0">
      </div>

       <example-paragraph></example-paragraph>
    </div>
  </div>
</section>
</template>

<script>
import ExampleGoalKeeper from "@/components/main/ExampleGoalKeeper";
import {onMounted, reactive} from "vue";
import axios from "axios";
import DefenderPers from "@/components/main/TeamPerspolise/DefenderPers";
import AttackPers from "@/components/main/TeamPerspolise/AttackPers";
import MainAttack from '@/components/main/TeamPerspolise/MainAttack'
import ExampleParagraph from "@/components/main/ExampleParagraph";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "PlayersPers",
  components: {ExampleParagraph, AttackPers, DefenderPers, ExampleGoalKeeper,MainAttack},
  setup(){
    const result=reactive([]);
    const total=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/6228'),
        axios.get('https://api.footballi.net/api/v2/player/2226218'),
        axios.get('https://api.footballi.net/api/v2/player/2448562'),
        axios.get('https://api.footballi.net/api/v2/player/2448565'),
      ])
          .then(axios.spread((...response)=>{
            result.push(response)
            console.log(response)
          }))
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      result,total
    }
  }
}
</script>

